const p1 = {
  ru: '–†–æ–∂–¥–µ–Ω–Ω—ã–µ –≤ –¥–≤–∞–¥—Ü–∞—Ç—å —Å–µ–¥—å–º–æ–π –ª—É–Ω–Ω—ã–π –¥–µ–Ω—å ‚Äì –º—É–¥—Ä–µ—Ü—ã, —á–∞—Å—Ç–æ —É–¥–∏–≤–ª—è—é—â–∏–µ –æ–∫—Ä—É–∂–∞—é—â–∏—Ö —Å–≤–æ–∏–º–∏ –ø–æ—Å—Ç—É–ø–∫–∞–º–∏ –∏ –º—ã—Å–ª—è–º–∏. –í —Ç–µ—á–µ–Ω–∏–µ –∂–∏–∑–Ω–∏ –ø–æ—Å—Ç–æ—è–Ω–Ω–æ –¥—É—Ö–æ–≤–Ω–æ —Ä–∞—Å—Ç—É—Ç, —Å–æ–≤–µ—Ä—à–∞—è –∫–∞—á–µ—Å—Ç–≤–µ–Ω–Ω—ã–µ –ø–µ—Ä–µ—Ö–æ–¥—ã –Ω–∞ –Ω–æ–≤—ã–µ —É—Ä–æ–≤–Ω–∏ —Ä–∞–∑–≤–∏—Ç–∏—è. –†–∞–∑—É–º–µ–µ—Ç—Å—è, —Ç–∞–∫–∏–µ ¬´—Å—Ç—É–ø–µ–Ω—å–∫–∏¬ª —á–∞—Å—Ç–æ –±—ã–≤–∞—é—Ç –±–æ–ª–µ–∑–Ω–µ–Ω–Ω—ã–º–∏ –∏ —Å–æ–ø—Ä–æ–≤–æ–∂–¥–∞—é—Ç—Å—è —Ä–∞–∑–≤–µ–Ω—á–∏–≤–∞–Ω–∏–µ–º –∏–ª–ª—é–∑–∏–π. <b>–ü—Ä–∏–Ω–∏–º–∞–π—Ç–µ —É—Ä–æ–∫–∏ –∂–∏–∑–Ω–∏ —Å –±–ª–∞–≥–æ–¥–∞—Ä–Ω–æ—Å—Ç—å—é</b>, –≤–µ–¥—å –∏–º–µ–Ω–Ω–æ –æ–Ω–∏ –¥–∞—é—Ç –í–∞–º –≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç—å —É–ø—Ä–∞–≤–ª—è—Ç—å —Å–≤–æ–µ–π —Å—É–¥—å–±–æ–π, –¥–≤–∏–≥–∞—Ç—å—Å—è –≤ –≤—ã–±—Ä–∞–Ω–Ω–æ–º –í–∞–º–∏ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–∏.',
  en: 'Born on the twenty-seventh lunar day are wise men, often astonishing others with their actions and thoughts. Throughout life they constantly grow spiritually, making qualitative transitions to new levels of development. Of course, such "steps" are often painful and are accompanied by illusions debunking. <b>Take life\'s lessons with gratitude</b>, because they give you the opportunity to control your destiny, to move it in the direction you have chosen.'
}

const p2 = {
  ru: '<b>–í–∞–º –≤–∞–∂–Ω–æ —Ä–∞–∑–≤–∏–≤–∞—Ç—å –≤ —Å–µ–±–µ –æ—Ç–≤–µ—Ç—Å—Ç–≤–µ–Ω–Ω–æ—Å—Ç—å, –≤–∑–≤–µ—à–∏–≤–∞—Ç—å –ø—Ä–∏–Ω–∏–º–∞–µ–º—ã–µ —Ä–µ—à–µ–Ω–∏—è, –≤—ã–ø–æ–ª–Ω—è—Ç—å –æ–±–µ—â–∞–Ω–∏—è –∏ —Å–¥–µ—Ä–∂–∏–≤–∞—Ç—å –¥–∞–Ω–Ω–æ–µ —Å–ª–æ–≤–æ.</b> –≠—Ç–∏ –∫–∞—á–µ—Å—Ç–≤–∞ –ø–æ–º–æ–≥–∞—é—Ç –ª—é–¥—è–º –¥–≤–∞–¥—Ü–∞—Ç—å —Å–µ–¥—å–º—ã—Ö –ª—É–Ω–Ω—ã—Ö —Å—É—Ç–æ–∫ —É—Å–ø–µ—à–Ω–æ –ø—Ä–µ–æ–¥–æ–ª–µ–≤–∞—Ç—å –∂–∏–∑–Ω–µ–Ω–Ω—ã–µ –ø—Ä–µ–ø—è—Ç—Å—Ç–≤–∏—è –∏ —ç—Ñ—Ñ–µ–∫—Ç–∏–≤–Ω–æ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å –ø–æ–ª—É—á–µ–Ω–Ω—ã–µ –∑–Ω–∞–Ω–∏—è –Ω–∞ –ø—Ä–∞–∫—Ç–∏–∫–µ.',
  en: '<b>It is important for you to develop responsibility, weigh decisions, fulfill promises and keep your word.</b> These qualities help people of the twenty-seventh lunar day to successfully overcome life\'s obstacles and effectively use the gained knowledge in practice.'
}

const paragraphs = [p1, p2]
export default paragraphs



